}

.flex1{
    display: flex;
    align-items: center;
}
.logos img {
    height:30px;
    width:30px;
    margin: 0 7px;
    
}

.buttonb {
    padding: 12.5px 30px;
    height:50px;
    width:150px;
    border: 0;
    border-radius: 25px;
    background-color: #03B0FD;
    color: #151925;
    font-family: "Inter", sans-serif;
    font-weight: 600;
    font-size: 16px;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    margin-left:30px;
    cursor: pointer;
  }
  
  .buttonb:hover {
    background-color: #6fc5ff;
    box-shadow: 0 0 20px #6fc5ff50;
    transform: scale(1.1);
    
  }
  
  .buttonb:active {
    background-color: #3d94cf;
    transition: all 0.25s;
    -webkit-transition: all 0.25s;
    box-shadow: none;
    transform: scale(0.98);
  }
  .buttonc {
    cursor: pointer;
    padding: 12.5px 30px;
    height:50px;
    width:150px;
    border: 2px solid #03B0FD;
    border-radius: 25px;
    background-color: #151925;
    color: #03B0FD;
    font-family: "Inter", sans-serif;
    font-weight: 600;
    font-size: 16px;
    transition: all 0.5s;
    -webkit-transition: all 0.5s;
    pointer-events: auto;
  }
  
  .buttonc:hover {
    background-color: #151925;
    box-shadow: 0 0 20px #6fc5ff50;
    transform: scale(1.1);
  }
  
  .buttonc:active {
    background-color: #151925;
    transition: all 0.25s;
    -webkit-transition: all 0.25s;
    box-shadow: none;
    transform: scale(0.98);
  }
.shape {
    
    width: 100%;
    height: 100px; /* Adjust height as needed */
    fill: #3498db; /* Adjust color as needed */
  }


  img .behind_the_image{
    height:100px;
  }
  
  .profile_image_square {
    width: 300px;
    height: 300px;
    object-fit: cover;
    border-radius: 16px; /* keeps it square with slightly rounded corners */
    border: 4px solid #00cefc;
    box-shadow: 0 4px 20px rgba(0, 255, 255, 0.3);
    transition: transform 0.3s ease;
  }
  
  .profile_image_square:hover {
    transform: scale(1.05);
  }
  
  .right_main img {
    height: 250px;
    width: 250px;
    border-radius: 15px; /* Square shape with slight rounding */
    margin-top: 80px; /* Pushes the image down to align with text */
    object-fit: cover;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.4);
  }
  
  @media (max-width: 768px) {
    .navbar {
      flex-direction: column;
      height: auto;
      padding: 10px 0;
    }
  
    .left, .right {
      width: 100%;
      justify-content: center;
      margin: 5px 0;
    }
  
    .button {
      width: auto;
      font-size: 16px;
      margin: 5px;
    }
  
    .Main {
      flex-direction: column-reverse;
      align-items: center;
      margin-top: 100px;
    }
  
    .main_left, .right_main {
      width: 90%;
      text-align: center;
      margin: 10px 0;
    }
  
    .names {
      font-size: 40px;
    }
  
    .i_am_a, .follow_me, .it_is_me {
      font-size: 18px;
      text-align: center;
    }
  
    .right_main img {
      width: 200px;
      height: 200px;
      margin-top: 20px;
    }
  
    .buttonb, .buttonc {
      width: 130px;
      height: 45px;
      font-size: 14px;
      margin: 10px;
    }
  
    .logos img {
      height: 24px;
      width: 24px;
      margin: 0 5px;
    }
  }
  